import React, { useState, useEffect } from 'react'
import Input from "../../../../UI/components/input/Input";
import Button from "../../../../UI/components/button/Button";
import Spinner from '../../../spinner/Spinner';
import { useTranslation } from "react-i18next";
import { validateEmail } from '../../../../utils/validate-utils';
import { useErrors } from '../../../../hooks/errors-hook';
import axios from 'axios';
import './Follow.scss'


function Follow({ setDone }) {
  const [form, setForm] = useState({
    name: '',
    email: ''
  })
  const [canSend, setCanSend] = useState(false)
  const [loading, setLoading] = useState(false)

  const { t } = useTranslation()

  useEffect(() => {
    if (form.name.trim().length && form.email.trim().length) {
      setCanSend(true)
    } else {
      setCanSend(false)
    }
  }, [form])

  const {
    arrayOfErrors,
    clearErrors,
    onSetError,
    onSetErrorsFromRequest
  } = useErrors()

  const sendForm = async () => {
    try {
      clearErrors()
      if (validateEmail(form.email)) {
        setLoading(true)
        const formData = new FormData()
        formData.append('email', form.email)
        formData.append('name', form.name)
        axios.post(process.env.REACT_APP_BASE_URL + '/api/casting/subscribe', formData)
          .then(response => {
            if (response.status) {
              clearErrors()
              setDone(true)
              setTimeout(() => setDone(false), 3000)
            }
          })
          .catch(err => console.log(err))
          .finally(() => setLoading(false))
      } else {
        onSetError('email', 'Email is not valid!')
      }

    } catch (err) {
      const resErrors = err.response?.data.errors
      onSetErrorsFromRequest(resErrors)
    }
  }

  const formChange = e => setForm({ ...form, [e.target.name]: e.target.value })

  return (
    <>
      <h2 className="follow__title">
        <span className="befree-title-blue">{t('follow.title.blue')}&nbsp;</span>{t('follow.title')}
      </h2>
      <form className="follow" onSubmit={sendForm}>
        <div className="error_wrapper">
          {arrayOfErrors.map((error, idx) =>
            <p key={idx} className="input_error error_visible">{error[0]}</p>
          )}
        </div>
        <Input
          error={
            arrayOfErrors?.name && arrayOfErrors?.name[0].length > 0
              ? arrayOfErrors?.name[0]
              : null
          }
          changeHandler={formChange}
          name="name"
          placeholder={t('name')}
          value={form.name}
        />
        <Input
          error={
            arrayOfErrors?.email && arrayOfErrors?.email[0].length > 0
              ? arrayOfErrors?.email[0]
              : null
          }
          changeHandler={formChange}
          name="email"
          placeholder={t('email')}
          value={form.email}
        />
        <div style={{ width: '100%', marginTop: '-25px' }}>
          {loading ? <Spinner /> : <Button onClick={sendForm} disabled={!canSend} title={t('send')} />}
        </div>
      </form>
    </>
  )
}

export default Follow
